<template>
  <div class="where mt-5">
    <div class="content p-5 mx-auto">
      <div class="banner"></div>
      <div class="where text-left mt-5">
        <div class="title-text text-left">BeBe去哪兒</div>
        <div class="d-flex flex-wrap flex-md-nowrap">
          <div class="w-half mr-1 mt-1 border p-3 position-relative" v-for="(item, index) in whereList" :key="index">
            <div @click="openmodal(item)">
              <p>{{ item.title }}</p>
              <p>{{ item.day }}</p>
            </div>
            <div v-if="item.status !== ''" class="cover-frame text-white d-flex align-items-center justify-content-center position-absolute bg-red h-100 w-100">
              <p class="font-weight-bold">{{ item.status }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="frame" class="frame-modal p-4 d-flex align-items-center">
    <div class="frame bg-white w-100 mx-auto p-5">
      <button @click="frame = false">close</button>
      <div>{{ modaltitle }}</div>
      <div>{{ modalday }}</div>
      <div>{{ modalcontent }}</div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'where',
  data () {
    return {
      modalcontent: '',
      modaltitle: '',
      modalday: '',
      frame: false,
      whereList: [
        { title: '1111', day: '11/2', content: 'whereList', year: 2021, month: 1, date: 1, deadyear: 2021, deadmonth: 1, deaddate: 5, status: '' },
        { title: '2222', day: '11/2', content: 'whereList', year: 2021, month: 1, date: 1, deadyear: 2021, deadmonth: 9, deaddate: 9, status: '' },
        { title: '3333', day: '11/2', content: 'whereList', year: 2021, month: 1, date: 9, deadyear: 2021, deadmonth: 1, deaddate: 12, status: '' },
        { title: '4444', day: '11/2', content: 'whereList', year: 2021, month: 1, date: 1, deadyear: 2021, deadmonth: 1, deaddate: 9, status: '' },
        { title: '5555', day: '11/2', content: 'whereList', year: 2021, month: 1, date: 1, deadyear: 2021, deadmonth: 1, deaddate: 9, status: '' }
      ],
      year: new Date().getFullYear(),
      month: new Date().getMonth() + 1,
      date: new Date().getDate()
    }
  },
  created () {
    this.$nextTick(() => {
      this.dateShow()
    })
  },
  methods: {
    dateShow () {
      for (let i = 0; i < this.whereList.length; i++) {
        if (this.whereList[i].year > this.year || (this.whereList[i].year >= this.year && this.whereList[i].month > this.month) || (this.whereList[i].year >= this.year && this.whereList[i].month >= this.month && this.whereList[i].date > this.date)) {
          this.whereList[i].status = `敬請期待`
        } else if (this.year > this.whereList[i].deadyear || (this.year >= this.whereList[i].deadyear && this.month > this.whereList[i].deadmonth) || (this.year >= this.whereList[i].deadyear && this.month >= this.whereList[i].deadmonth && this.date > this.whereList[i].deaddate)) {
          this.whereList[i].status = `回合結束`
        }
      }
    },
    openmodal (item) {
      this.frame = true
      this.modalcontent = item.content
      this.modalday = item.day
      this.modaltitle = item.title
    }
  }
}
</script>

<style>

</style>



